<template>
    <div class="overlay-content" style="border: solid">
        <div>
            <h1>{{filename}}을 편집하세요.</h1>
        </div>
        <!-- <p @click="click('닫기')">닫기</p> -->
        <!-- <div><p>{{data.row.title}}</p></div>
        <div v-for="(item, index) in data.rows" v-bind:key="index">
            <p @click="click('선택', item)">{{item.title}}</p>
        </div> -->
        <p style="background: red" @click="click('Post')">Post</p>
        <!-- <p style="background: blue" @click="click('Message')">메시지 발화</p>
        <p style="background: green" @click="click('Intent')">인텐트 요청</p>
        <p style="background: green" @click="click('Service')">서비스 요청</p> -->
        <!-- <input v-model="search" type = "text" name="userid" @keyup.enter="click('Search')">
        <input type="button" @click="click('Search')" value="검색"> -->
    </div>
</template>
<style scoped>
a{
  cursor:pointer;
}
p{
    color: black;
}
.overlay-content {
  position: relative;
  top: 25%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 36px;
  display: block;
  transition: 0.3s;
}

.overlay a:hover, .overlay a:focus {
  color: #f1f1f1;
}

.overlay .closebtn {
  position: absolute;
  top: 20px;
  right: 45px;
  font-size: 60px;
}
</style>

<script>
import responseType from '../responseType';
import directType from '../directType';
import intentType from '../intentType';
import common from '../../utils/common'

export default {
    name: 'Template', //SKelton:DefineComponentName
    props: {
        data: Object,
    },
    data: function(){
        return {
            search: '',
            filename: 'Template.vue', //MANGO:Filename
        }
    },
    mounted() {
    },    
    computed: {
    },
    filters: {
    },
    methods: {
        click(type, item){
            switch (type) {
                case 'Post':                  
                    {
                    // direct
                    const type = directType.template;
                    const params = {
                        seq: this.data.rows.length,
                    };                    

                    this.$store.dispatch('POST', {                      
                            directType: type,
                            params,
                        },
                    ).then((res) => {
                        console.log('result', res);
                        const {error, msg, rows} = res;
                        if(error > 0){
                            alert(`error: ${msg}`);
                            return;
                        }

                        this.data.rows = this.data.rows.concat(rows);
                    });
                    }
                    break;          
                case 'Message':                  
                    common.sendMessage(this.$t('Login'));
                    break;       
                case 'Intent':               
                    common.sendIntent(intentType.INT_LOGIN, {}, '인텐트 보내기');   
                    break;       
                case 'Service':                  
                    common.callResponse(responseType.Login, {}, '서비스 보내기');
                    break;       
                case 'Search':
                    {
                        const params = {
                            search: this.search
                        }
                        common.callResponse(responseType.ECHO, params);
                    }
                    break;
                //Skelton:AddClick
                default:                
                    this.$store.dispatch('CALL_RESPONSE', this.$t('Login'));
                    alert(item.title);
                break;
            }
        },
        close(){
            this.$uiHandler.closePopup();
        },
    },
};
</script>